function sliderServices() {
    var swiper = new Swiper('.services .swiper', {
        slidesPerView: "auto",
        spaceBetween: 32,
        loop: false,
        navigation: {
            nextEl: '.services .swiper-button-next',
            prevEl: '.services .swiper-button-prev',
        }
    })
}

let SlidersArray = []

function sliderMaterials() {
    $(".materials").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.materials[data-slider-id="${index}"] .swiper`, {
            slidesPerView: "auto",
            observer: true,
            observeParents: true,
            navigation: {
                nextEl: `.materials[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.materials[data-slider-id="${index}"] .swiper-button-prev`,
            }
        })
    })
}

$(document).ready(function() {

    sliderServices();
    sliderMaterials();


    // Burger - Menu
    $('.header-top__burger').click(function () {
        $(this).toggleClass('header-top__burger--active')
        $('.header-mob').toggleClass('header-mob--active')
        $('body').toggleClass('fixed-body')
    })

    // Hover - Click
    $(".header-bottom__products").hover(onIn, onOut);
    $(".header-bottom__products .header-bottom__products-show").click(function() {
        if ($(this).parents(".header-bottom__products").hasClass("header-bottom__products--active")) {
            $(this).parents(".header-bottom__products").removeClass("header-bottom__products--active")
        } else {
            $(this).parents(".header-bottom__products").addClass("header-bottom__products--active")
        }
    })

    let ItemShow = $('.header-bottom__products-show')
    let Item = $('.header-bottom__products')

    $(document).mouseup(function (e) {
        if ( !ItemShow.is(e.target) && ItemShow.has(e.target).length === 0 &&
            !Item.is(e.target) && Item.has(e.target).length === 0

        ) {
            Item.removeClass('header-bottom__products--active')
        }
    })

    // Hover - Click2
    $(".header-bottom__item").hover(onIn2, onOut2);
    $(".header-bottom__item .header-bottom__item-show").click(function() {
        if ($(this).parents(".header-bottom__item").hasClass("header-bottom__item--active")) {
            $(this).parents(".header-bottom__item").removeClass("header-bottom__item--active")
        } else {
            $(this).parents(".header-bottom__item").addClass("header-bottom__item--active")
        }
    })

    // Back
    $('.header-bottom__back').click(function () {
        $('.header-bottom__item').removeClass('header-bottom__item--active')
    })

    // Select
    $(".custom_select select").niceSelect()


    // Tabs
    $(".info-material__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".info-material__tab").removeClass("info-material__tab--active")
        $(this).addClass("info-material__tab--active")

        $(this).parents(".info-material__tabs").find(".info-material__content").removeClass("info-material__content--active")
        $(this).parents(".info-material__tabs").find(`.info-material__content[data-content-path="${path}"]`).addClass("info-material__content--active")
    })

})

function onIn() {
    if (window.innerWidth > 992) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-bottom__products--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 992) {
        $(this).removeClass("header-bottom__products--active")
    }
}

